import React from 'react'
import {Container, Row, Col} from 'react-bootstrap'
import CourseCard from './courseCard'
import './course.css'

const cards = [
  {id:1, img:'https://images.freeimages.com/images/large-previews/8f6/blocks-1426236.jpg', title:'程式邏輯啟發班', text:'我們用獨家研發的遊戲培養程式思維，從玩樂中打好基礎，未來學程式事半功倍!', link:''},
  {id:2, img:'https://upload.wikimedia.org/wikipedia/commons/thumb/d/d6/Scratch_Logo.svg/1200px-Scratch_Logo.svg.png', title:'Scratch實戰班', text:'藉由簡單的積木程式，寫出屬於自己的遊戲!', link:''},
  {id:3, img:'https://upload.wikimedia.org/wikipedia/commons/thumb/c/c2/C_slash_cpp.svg/1024px-C_slash_cpp.svg.png', title:'Cpp基礎班', text:'Cpp是工學院不可不修的一門課，想成為科技人才不可不學!', link:''},
  {id:4, img:'https://th.bing.com/th/id/R.443e356bcd15754dc2f877c572302578?rik=1z1TlXrCepJQYQ&pid=ImgRaw&r=0', title:'Python基礎班', text:'Python是目前世界上最多人使用的語言，AI、大數據分析都建立在Python上', link:''},
  {id:5, img:'https://upload.wikimedia.org/wikipedia/commons/thumb/d/d4/Javascript-shield.svg/1200px-Javascript-shield.svg.png', title:'JavaScript基礎班', text:'JavaScript是一款網頁開發必學的全端語言，大部分的網頁都可以看到JS的影子', link:''},
  {id:7, img:'https://th.bing.com/th/id/OIP.sZI4nPhjouUSsPBQhnO3BgHaFG?pid=ImgDet&rs=1', title:'網頁開發基礎班', text:'使用html、css、Js建立第一個網頁吧!', link:''},
  {id:8, img:'https://www.lifewire.com/thmb/sbIy0juVWjv2cdQPDLee-rPo-5E=/768x0/filters:no_upscale():max_bytes(150000):strip_icc()/8585049088_1b08b78529_o-57fede665f9b5805c29dc6f7.jpg', title:'App開發基礎班', text:'App已經成為生活的必需品，現在你也可以製作自己的App', link:''},
  {id:9, img:'https://th.bing.com/th/id/R.035fa302b15c1863bea5344ae4d1f2aa?rik=OdA%2bNDzNqK8gxA&pid=ImgRaw&r=0', title:'Minecraft程式營', text:'用世界暢銷遊戲Minecraft介紹程式邏輯和電路思維，在玩樂中學習!', link:''},
  {id:10, img:'https://th.bing.com/th/id/OIP.iOjd--kAdRkLGGQ0Y9KkUwHaFj?pid=ImgDet&rs=1', title:'魔法程式學院', text:'Coming Soon...', link:''},
]

export default function Course() {
  return (
    <Container fluid className='bg-light course-container py-3 px-0'>
      <Row>
        <Col sm={12} className='title-container py-3'>
          <h2><strong>課程介紹</strong></h2>
        </Col>
      </Row>
      <Row className='px-5'>
        {cards.map((cards)=>(
          <Col sm={12} md={6} lg={4} className='py-3'>
            <CourseCard key={cards.id} img={cards.img} title={cards.title} text={cards.text} link={cards.link} />
          </Col> 
        ))}
      </Row>
    </Container>
  )
}
